@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700);
body, button {
  font-family: "Open Sans", sans-serif;
}
#map {
  width: 100%;
  height: 768px;
}
.map-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.map-body .map-body-map {
    width: 100%;
    margin-right: 20px;
    position: relative;
}
.map-body .map-body-listing {
    width: 300px;
    max-height: 768px;
    overflow-y: scroll;
}
.ui-autocomplete {
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: 500px;
}
.marker-details {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: lightgrey;
  width: 300px;
  overflow-y: scroll;
}
.rating-item {
  border-bottom: 1px solid grey;
  margin-bottom: 22px;
}
.rating-item span {
    display: block;
}














































